import React, { Component } from 'react';
import './App.css';
import './nprogress.css';
import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import { extractLocations, getEvents, checkToken, getAccessToken } from './api';
import { OfflineAlert } from './Alert';
import WelcomeScreen from './WelcomeScreen';
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts';


class App extends Component {

  state = {
    events: [],
    locations: [],
    numberOfEvents: 32,
    currentLocation: 'all',
    networkStatusText: '',
    showWelcomeScreen: undefined
  }


  async componentDidMount() {
    this.mounted = true;
    const accessToken = localStorage.getItem('access_token');
    const isTokenValid = (await checkToken(accessToken)).error ? false : true;
    const searchParams = new URLSearchParams(window.location.search);
    const code = searchParams.get("code");
    this.setState({ showWelcomeScreen: !(code || isTokenValid) });

    if ((code || isTokenValid && this.mounted)) {
      getEvents().then((events) => {
        if (this.mounted) {
          this.setState({
            events,
            locations: extractLocations(events)
          });
        }
      });
    }
    if (!navigator.onLine) {
      this.setState({
        networkStatusText: 'You are offline, loading data from last connection'
      })
    } else {
      this.setState({
        networkStatusText: ''
      })
    }
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  updateEvents = (location) => {
    getEvents().then((events) => {
      const locationEvents = (location === 'all') ?
        events :
        events.filter((event) => event.location === location);

      this.setState({
        events: locationEvents.slice(0, this.state.numberOfEvents),
        currentLocation: location
      });
      console.log('in updateEvents, numberOfEvents=' + this.state.numberOfEvents)
    });
  }

  updateNumberOfEvents = (newNumber) => {

    this.setState({
      numberOfEvents: newNumber
    });
    console.log('in updateNumberOfEvents, numberOfEvents=' + this.state.numberOfEvents)
    this.updateEvents(this.state.currentLocation);
  }

  getData = () => {
    const { locations, events } = this.state;
    const data = locations.map((location) => {
      const number = events.filter((event) => event.location === location).length;
      const city = location.split(',').shift();
      return { city, number };
    })
    return data;
  };

  render() {

    if (this.state.showWelcomeScreen === undefined) return <div className="App" />

    return (
      <div className="App">
        {/* Pass in updateEvents method as a prop to CitySearch so that
        you can call it inside handleItemClicked */}
        <p>Search City</p>
        <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} />
        <br /><br /><br />
        <p>Number of Events</p>
        <NumberOfEvents numberOfEvents={this.state.numberOfEvents} updateNumberOfEvents={this.updateNumberOfEvents} />
        <br /><br /><br />
        <OfflineAlert text={this.state.networkStatusText} />
        <br />
        <ScatterChart width={730} height={250}
          margin={{ top: 20, right: 20, bottom: 10, left: 10 }}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="city" name="city" type="category" />
          <YAxis dataKey="number" name="number of events" type="number" allowDecimals={false} />
          <Tooltip cursor={{ strokeDasharray: '3 3' }} />
          <Scatter data={this.getData()} fill="#8884d8" />
        </ScatterChart>
        <br />
        <p> Events List</p>
        <EventList events={this.state.events} />
        <br />
        <WelcomeScreen showWelcomeScreen={this.state.showWelcomeScreen} getAccessToken={() => { getAccessToken() }} />
      </div>
    );

  }
}

export default App;